import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';
import Search from './Search';
import $ from 'jquery';
class Header extends Component {

	componentDidMount(){
		// to change css of layout selectors
		$(document).ready(function(){
			$(".3colview").css("background-color", "#1b2e9d")
    		$(".3colview").on("click",function(){
       			$(this).css("background-color", "#1b2e9d");
       			$(".4colview").css("background-color", "transparent")
   			});
   			$(".4colview").on("click",function(){
       			$(this).css("background-color", "#1b2e9d");
       			$(".3colview").css("background-color", "transparent")
   			});
		});

		// to change css of sort buttons
		$(document).ready(function(){
			$(".likeSort").css("background-color", "transparent")
			$(".trendSort").css("background-color", "transparent")
    		$(".likeSort").on("click",function(){
       			$(this).css("background-color", "#1b2e9d");
       			$(".trendSort").css("background-color", "transparent")
   			});
   			$(".trendSort").on("click",function(){
       			$(this).css("background-color", "#1b2e9d");
       			$(".likeSort").css("background-color", "transparent")
   			});
		});

	}


	render() {
		return(				
			<div className="container-fluid">
				<div className="row header-section">
					<div className="col-md-2 col-lg-2  header-logo" data-toggle="tooltip" data-placement="top" title="Risorso">
						<a href="#"><img src="../assets/images/main_logo.png" /></a>
					</div>
					<div className="col-md-6 col-lg-6">
						<Search />
					</div>
					
					<div className="col-md-2 col-lg-2">
						<ul className="list-icons">
							<li><i className="ico-icon30 3colview" aria-hidden="true" data-toggle="tooltip" data-placement="top" title="3 Column View" onClick={this.props.layout3}></i></li>
							<li><i className="ico-icon29 4colview" aria-hidden="true" data-toggle="tooltip" data-placement="top" title="4 Column View" onClick={this.props.layout4}></i></li>
							<li><i className="ico-icon28 likeSort" aria-hidden="true" data-toggle="tooltip" data-placement="top" title="Sort by Likes" onClick={this.props.likesort}></i></li>
							<li><i className="ico-icon27 trendSort" aria-hidden="true" data-toggle="tooltip" data-placement="top" title="Sort By Trending" onClick={this.props.trendsort}></i></li>
						</ul>
					</div>
					<div className="col-md-2 col-lg-2 video-header">
						<div className="dropdown" data-toggle="tooltip" data-placement="top" title="Videos">
							<button className="btn dropdown-toggle video-button" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
							Videos
							</button>
							<div className="dropdown-menu video-list" aria-labelledby="dropdownMenuButton">
								<ul className="menu_list">
									<li className="list_item"><a className="dropdown-item" href="#">Videos</a></li>
									<li className="list_item"><a className="dropdown-item" href="#">Images</a></li>
									<li className="list_item"><a className="dropdown-item" href="#">List</a></li>
								</ul>
							</div>
						</div>
					</div>
				</div>
			</div>
		)
	}
}

export default Header;